<template>
  <div class="body">
    <h1 v-if="isRegister">欢迎注册小张的网站</h1>
    <h1 v-else>欢迎登录小张的网站</h1>

    <div class="login-form">
      <div class="avtar">
        <img src="./images/avtar.png" />
      </div>
      <form>
        <input v-model.trim="username" type="text" class="text" placeholder="你的账号" />
        <div>
          <input v-model.trim="password" type="password" placeholder="你的密码" />
        </div>
      </form>
      <div class="signin">
        <input type="submit" value="注册" v-if="isRegister" @click="register()" />
        <input type="submit" value="登录" v-else @click=" login()" />
      </div>
    </div>
  </div>
</template>
<script>
require("./js/jquery.min.js");
export default {
  computed: {
    isRegister() {
      return this.$route.path == "/register";
    },
  },
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    register() {
      let that=this
      axios.get(`/api/register?username=${that.username}&password=${that.password}`).then(r=>{
        if(r.data.code==200){
          that.$message({
            message:'注册成功',
            type:'success'
          })
        }else{
           that.$message({
            message:'注册失败：'+r.data.data,
            type:'error'
          })
        }
      })

    },
    login() {
      let that = this;
      axios
        .get(`/api/login?username=${that.username}&password=${that.password}`)
        .then((r) => {
          if (r.data.code == 200) {
            that.$message({
              message: `登录成功`,
              type: "success",
            });
            that.$router.push("/");
          } else {
            that.$message({
              message: `登录失败：${r.data.data}`,
              type: "error",
            });
          }
        });
    },
  },
};
</script>


<style scoped>
@import url(./css/style.css);

.body {
  margin-top: -60px;
}
</style>



